.menu {
  background-color: var(--background-color-light);
  border-radius: var(--border-radius-small);
  border: 1px solid var(--background-color-black);
  overflow: auto;
  max-height: 200px;
  position: absolute;
  box-shadow: 0px 8px 8px 4px var(--background-color-dark);
  z-index: 4;
}
